// 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 
import store from '@/store/index.js';
import { getNewDisc } from '@/request/api/discoverMusic.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 本周新碟
      weekData: [],
      // 本月新碟
      monthData: [],
      // 当前地区
      nowArea: 'ALL',
      // 当前 type 参数 new 和 hot 请求回来的数据一样
      nowType: 'hot',
      // 请求回来的原数据
      resData: [],
      // 每次插入渲染的 DOM 数量
      once: 15,
      // 渲染总数
      totalWeek: 0,
      totalMonth: 0,
      // 当前已经渲染 DOM 的总数
      countOfRenderWeek: 20,
      countOfRenderMonth: 30
    };
  },

  // 接收父组件传过来的值
  props: ['scrollTop'],

  async mounted() {
    // 存储名为拼接字符串
    let vuelsName = 'getALLhotNewDisc'; // 在 Local Storage(本地存储) 中检查是否有

    let vuelsNewDisc = this.$ls.get(vuelsName, 'null');
    console.log(vuelsNewDisc); // 在本地中，不用重新获取

    if (vuelsNewDisc != 'null') this.resData = vuelsNewDisc.data; // 本地已删除，则重新获取

    if (vuelsNewDisc == 'null') {
      // 获取新碟上架专辑 第一次页面创建完默认申请 'ALL' 'hot'
      let res = await getNewDisc('ALL', 'hot'); // console.log(res);
      // 转存到本地，有效期为 10 分钟

      this.$ls.set(vuelsName, res, store.state.validityTime); // 把请求回来的原数据放到存放原数据的暂存数组中(该数组不是渲染数据的数组)

      this.resData = res.data;
    } // 渲染总数


    this.totalWeek = this.resData.weekData.length;
    this.totalMonth = this.resData.monthData.length; // 刚开始只渲染部分数据

    this.weekData = this.resData.weekData.slice(0, 20);
    this.monthData = this.resData.monthData.slice(0, 30);
  },

  methods: {
    // 点击不同语种不同筛选类型，请求不同的数据
    async change(area, type) {
      this.checkAreaType(area, type); // 调用排他方法

      this.areaExclusive(area);
      this.typeExclusive(type);
    },

    // 请求不同地区的专辑，按不同的类型存到本地，封装为方法，传入专辑地区 area 和选择范围 type
    async checkAreaType(area, type) {
      // 更新当前地区和当前范围
      this.nowArea = area;
      this.nowType = type; // 存储名为拼接字符串

      let vuelsName = 'get' + area + type + 'NewDisc'; // 在 Local Storage(本地存储) 中检查是否有

      let vuelsAreaTypeNewDiscs = this.$ls.get(vuelsName, 'null');
      console.log(vuelsAreaTypeNewDiscs); // 在本地中，不用重新获取，直接把本地数据转存到存放原数据的 resData[]

      if (vuelsAreaTypeNewDiscs != 'null') this.resData = vuelsAreaTypeNewDiscs.data; // 本地已删除，则重新获取

      if (vuelsAreaTypeNewDiscs == 'null') {
        // 获取新碟上架当前选择地区和范围的专辑
        let res = await getNewDisc(this.nowArea, this.nowType); // console.log(res);

        try {
          // 转存到本地，有效期为 10 分钟
          this.$ls.set(vuelsName, res, store.state.validityTime);
        } catch (e) {
          // 产生的异常最后一个单词为 quota，表示 localstorage 容量不够，清空所有 key，再发起数据请求
          if (e.name.toUpperCase().indexOf('QUOTA') >= 0) this.$ls.clear();
          this.$ls.set(vuelsName, res, store.state.validityTime);
        } // 把请求回来的原数据放到存放原数据的暂存数组中(该数组不是渲染数据的数组)


        this.resData = res.data;
      }

      if (area === 'ALL' && type === 'hot') {
        this.weekData = this.resData.weekData.slice(0, 20);
        this.countOfRenderWeek = 20;
        this.totalWeek = this.resData.weekData.length;
      } // 切换页面后刚开始也只渲染部分数据


      this.monthData = this.resData.monthData.slice(0, 30); // 重置当前已经渲染 DOM 的总数

      this.countOfRenderMonth = 30; // 渲染总数

      this.totalMonth = this.resData.monthData.length;
    },

    // 排他，传入当前 active 的 area ，只有该地区保留 active 类
    areaExclusive(area) {
      this.$refs.areaALL.classList.remove('top_left_active');
      this.$refs.areaZH.classList.remove('top_left_active');
      this.$refs.areaEA.classList.remove('top_left_active');
      this.$refs.areaKR.classList.remove('top_left_active');
      this.$refs.areaJP.classList.remove('top_left_active');
      if (area == 'ALL') this.$refs.areaALL.classList.add('top_left_active');
      if (area == 'ZH') this.$refs.areaZH.classList.add('top_left_active');
      if (area == 'EA') this.$refs.areaEA.classList.add('top_left_active');
      if (area == 'KR') this.$refs.areaKR.classList.add('top_left_active');
      if (area == 'JP') this.$refs.areaJP.classList.add('top_left_active');
    },

    typeExclusive(type) {
      this.$refs.typeHot.classList.remove('top_right_active');
      this.$refs.typeAll.classList.remove('top_right_active');
      if (type == 'hot') this.$refs.typeHot.classList.add('top_right_active');
      if (type == 'all') this.$refs.typeAll.classList.add('top_right_active');
    },

    // 滚动父盒子一段距离(scrollTop 改变的时候)往渲染数据的数组增加数据
    scrollLoadWeek(area, type) {
      if (area === 'ALL' && type === 'hot') {
        const tempWeek = [];

        for (let i = 0; i < this.once; i++) {
          // 当 DOM 渲染完就退出
          if (this.countOfRenderWeek >= this.totalWeek) return; // 把请求回来的原数据先加到暂存数组中

          tempWeek.push(this.resData.weekData[this.countOfRenderWeek]); // 当前已经渲染 DOM 的总数

          this.countOfRenderWeek += 1;
        } // 然后把暂存数组的数据加到需要渲染的数组中，concat() 将一个数组合并到另一个数组


        this.weekData = this.weekData.concat(tempWeek); // console.log(tempWeek);
      }
    },

    scrollLoadMonth() {
      const tempMonth = [];

      for (let i = 0; i < this.once; i++) {
        if (this.countOfRenderMonth >= this.totalMonth) return;
        tempMonth.push(this.resData.monthData[this.countOfRenderMonth]);
        this.countOfRenderMonth += 1;
      }

      this.monthData = this.monthData.concat(tempMonth); // console.log(tempMonth);
    },

    // 新碟显示播放键
    playKeyMouseover(choose, index) {
      if (choose == 1) this.$refs.playKey[index].classList = 'playKey';else this.$refs.playKey1[index].classList = 'playKey';
    },

    // 新碟隐藏播放键
    playKeyMouseleave(choose, index) {
      if (choose == 1) this.$refs.playKey[index].classList = 'playKey1';else this.$refs.playKey1[index].classList = 'playKey1';
    },

    // 去往专辑详情页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 2
        }
      });
    }

  },
  watch: {
    // 从父组件接收回来的 scrollTop 改变的时候才会触发
    scrollTop: function () {
      // console.log('子：' + this.scrollTop);
      this.scrollLoadWeek(this.nowArea, this.nowType); // 当上面的方法结束再执行下面的方法

      if (this.countOfRenderWeek >= this.totalWeek) this.scrollLoadMonth();
    },

    // 有渲染的数据时关闭加载组件
    weekData() {
      if (this.weekData.length !== 0) this.isLoading = 0;
    }

  }
};